import { createStore , combineReducers , applyMiddleware} from 'redux'
import thunk from 'redux-thunk'

import LS from './localStorage'

const nilaiDefaultLoginInfo = {
    api_token:'',
    loading:false,
    user:{fullname:'',username:'',api_token:''},
}
const nilaiDefaultDashboard = {
    page:'login',
    api_token:false,
    
	modalContent:{
		body:'',header:'',footer:'',
	},
	modalOpen:false,
	modalSize:'sm',
	modalContent2:{
		body:'',header:'',footer:'',
	},
	modalOpen2:false,
	modalSize2:'sm',
}
const loginInfo = ( state = nilaiDefaultLoginInfo ,action) => {
	switch(action.type){
        case 'RESET':
            state = {...nilaiDefaultLoginInfo,loading:true} ;
            document.body.classList.add("login");
            break;
		case 'CHANGE_api_token':
			if(!action.value){
				LS.remove('api_token');
			}else{
				LS.set('api_token', action.value);
			}
			window.helmi.api_token = action.value ;
			state = {...state , api_token : action.value };
            break;
        case 'update_user':
            state = {...state, user:action.value};
            break;
		case 'LOAD_CONFIG':
			let api_token = LS.get('api_token');
			state = {...state ,api_token,loading:true };
			break;
		default:break;
	}
	return state;
};
const dashBoard = (state = nilaiDefaultDashboard ,action)=>{
	switch(action.type){
        case 'RESET':
            state = {...nilaiDefaultDashboard,loading:true} ;
            break;
        case 'update_page': 
            state={...state,page:action.value};
            break;
		//======================== MODAL ================================
		case 'TOGGLE_MODAL':
			state = {...state,modalOpen:(action.value === 'false' ? false : true ) };
			break;
		case 'UPDATE_MODAL_BODY':
			state = {...state,modalContent:action.value};
			break;
		case 'UPDATE_MODAL_SIZE':
			state = {...state,modalSize:(action.value === 'lg' ? 'lg' : 'sm')};
			break;
		case 'TOGGLE_MODAL2':
			state = {...state,modalOpen2:( action.value ? true: false ) };
			break;
		case 'UPDATE_MODAL_BODY2':
			state = {...state,modalContent2:action.value};
			break;
		case 'UPDATE_MODAL_SIZE2':
			state = {...state,modalSize2:(action.value === 'lg' ? 'lg' : 'sm')};
			break;
		default:break;
	}
	return state;
};
const middleware = applyMiddleware(thunk);

const reducers = combineReducers({
	dashBoard,loginInfo
});
const store = createStore(reducers,middleware);
export default store;



