import React, {Component} from 'react'
import {connect} from 'react-redux'

import Alert from '../part/alert'
import ModalPage from '../part/modalpage'
import $ from 'jquery'
$.autocomplete = require('jquery-ui/ui/widgets/autocomplete')

class Player extends Component{
    state ={
        listData:false,
        errorList:[],
    }
    componentWillUnmount(){
        $(document).off('submit','[data-tombol="form"]')
        $(document).off('click','[data-tombol]')
        $('input[name="game_name"]').autocomplete( "destroy" );
    }
    __loadAutoComplete(){
        $('.modal input[name="game_name"]').autocomplete({
            appendTo: ".ui-front",
            source: (req , add)=>{
                let data = { search :req.term }
                $.ajax({
                    url:window.helmi.api + 'api/v1/player/get_complete',
                    dataType:'json',
                    data,
                    beforeSend: req =>{
                        $('.modal input[name="game_name"]').attr('disabled','disabled')
                        req.setRequestHeader("Authorization", 'Bearer '+ this.props.loginInfo.user.api_token );
                    },
                    complete: res =>{
                        $('.modal input[name="game_name"]').removeAttr('disabled')
                    }
                }).then(res =>{
                    if(res.data){
                        let {data} = res ;
                        data.forEach( val =>{
                            val.label = val.game_name ;
                        })
                        add(data)
                    }
                }).catch(res =>{
                    if(res.responseJSON.errors){
                        let {errors} = res.responseJSON,val={};
                        errors.forEach( valz => val=valz )
                        $('.modal #pesan-error').html(`
                           <div class="alert alert-danger">
                               <strong>Error : </strong>${val.message ? val.message : ''}
                               <button class="close" data-tombol="remove-alert">&times;</button>
                           </div>
                       `);
                    }
                })
            },
            select: ( e , item)=>{
                $('.modal input[name="game_id"]').val(item.item.id)
            }
        });
    }
    componentDidMount(){
        window.helmi.that = this
        let {that} = window.helmi;
        this.___loadData();
        this.props.dispatch({
            type:'UPDATE_MODAL_BODY2',value:{
                header:'Tambah Player',
                body:`
                    <div class="row">
                        <form data-tombol="form">
                            <div class="input-group">
                                <label class="input-group-addon"> Nama Player</label>
                                <input class="form-control" name="player_name" disabled />
                            </div>
                            <div class="input-group">
                                <label class="input-group-addon"> Game</label>
                                <input class="form-control" name="game_name" disabled placeholder="Cari game yang dimainkan" />
                            </div>
                            <input type="hidden" name="game_id" />
                            <input type="hidden" name="id" />
                            <input type="hidden" name="mode" value="tambah" />
                        </form>
                        <div id="pesan-error"></div>
                        <div class="ui-front"></div>
                    </div>
                `,
                footer:'<button disabled data-tombol="simpan" class="pull-left btn btn-danger">Simpan</button>'
            }
        });
        $(document).on('click','[data-tombol]',function(){
            let id;
            switch($(this).attr('data-tombol')){
                case 'remove-alert':
                    $(this).parents('.alert').hide()
                    break;
                case 'tambah':
                    that.props.dispatch(e=>{
                        e({type:'UPDATE_MODAL_SIZE2',value:'sm'})
                        e({type:'TOGGLE_MODAL2',value:true})
                        
                        $('.modal button,.modal input,.modal select,.modal textarea').removeAttr('disabled') 
                    })
                    that.__loadAutoComplete()
                    break;
                case 'edit':
                id = typeof $(this).attr('data-id') !== 'undefined' ? $(this).attr('data-id') : '';
                that.props.dispatch(e=>{
                    e({type:'UPDATE_MODAL_SIZE2',value:'sm'})
                    e({type:'TOGGLE_MODAL2',value:true})
                    $('.modal .modal-title').html('<div><h4>Edit ID : '+id+' </h4></div>')
                    $.ajax({
                        url:window.helmi.api + 'api/v1/player/get/'+id,
                        beforeSend:function(header){
                            header.setRequestHeader("Authorization", 'Bearer '+ that.props.loginInfo.user.api_token );
                        },
                        success:function(res){
                            if(res.data){
                                res.data.mode = 'edit'
                                $.each(res.data , (k,val) =>{
                                    $('.modal input[name="'+k+'"]').val(val);
                                })
                                
                                $('.modal button,.modal input,.modal select,.modal textarea').removeAttr('disabled')
                                that.__loadAutoComplete()
                            }
                        }
                    })
                })
                    break;
                case 'hapus':
                id = typeof $(this).attr('data-id') !== 'undefined' ? $(this).attr('data-id') : '';
                that.props.dispatch(e=>{
                    e({type:'UPDATE_MODAL_SIZE2',value:'sm'})
                    e({type:'TOGGLE_MODAL2',value:true})
                    $('.modal .modal-title').html('<div><h4>Hapus ID : '+id+' </h4></div>')
                    $('.modal .modal-body').html(`
                        <div style="text-align:center;">
                            <form data-tombol="form">
                                <input type="hidden" name="mode" value="delete"  />
                                <input type="hidden" name="id" />
                                <input type="hidden" name="game_id" value="asal" />
                                <input type="hidden" name="player_name" value="asal" />
                            </form>
                            <button data-tombol="simpan" class="btn btn-warning">DELETE</button>
                        </div>
                        <div id="pesan-error"></div>
                    `)
                    $('.modal .modal-body input[name="id"]').val(id)
                })
                    
                    
                    break;
                case 'modify-point':
                id = typeof $(this).attr('data-id') !== 'undefined' ? $(this).attr('data-id') : '';
                let point = typeof $(this).attr('data-point') !== 'undefined' ? $(this).attr('data-point') : 0;
                that.props.dispatch(e=>{
                    e({type:'UPDATE_MODAL_SIZE2',value:'sm'})
                    e({type:'TOGGLE_MODAL2',value:true})
                    $('.modal .modal-title').html('<div><h4>Modify Points For Player : '+id+' </h4></div>')
                    $('.modal .modal-body').html(`
                        <div style="text-align:center;">
                            <div class="input-group" data-id="${id}">
                                <label class="input-group-addon"><button data-tombol="kurangi" class="btn btn-info"> - </button> </label>
                                <input class="form-control" name="point" readonly />
                                <label class="input-group-addon"><button data-tombol="tambahi" class="btn btn-info"> + </button></label>
                            </div>
                        </div>
                        <div id="pesan-error"></div>
                    `)
                    $('.modal .modal-body input[name="point"]').val(point)
                })
                    break;
                case 'kurangi':
                case 'tambahi':
                    let mode = $(this).attr('data-tombol')
                    id = $(this).parents('.input-group').attr('data-id');
                    $.ajax({
                        url:window.helmi.api + 'api/v1/player/set-point/'+mode+'/'+id,
                        dataType:'json',
                        beforeSend:function(header){
                            header.setRequestHeader("Authorization", 'Bearer '+ that.props.loginInfo.user.api_token );
                        },success:function(res){
                            $('.modal #pesan-error').html(`
                                <div class="alert alert-success">
                                    <strong>Success : </strong>${res.message ? res.message : ''}
                                    <button class="close" data-tombol="remove-alert">&times;</button>
                                </div>
                            `);
                            if(res.data.points){
                                $('.modal .modal-body input[name="point"]').val(res.data.points)
                            }
                        }
                    })
                    break;
                case 'simpan':
                    $('.modal form').trigger('submit');
                    break;
                default:break;
            }
        })
        $(document).on('submit','[data-tombol="form"]',function(e){
            e.preventDefault()
            let formData = $('.modal form').serializeArray(),data={};
            formData.forEach( val =>{
                data[val.name] = val.value
            })
            
            $.ajax({
                url:window.helmi.api + 'api/v1/player/insert_update_delete',
                type:'POST',
                data:data,
                dataType:'json',
                beforeSend:function(header){
                    header.setRequestHeader("Authorization", 'Bearer '+ that.props.loginInfo.user.api_token );
                },
                success:function(res){
                    if(res.status==='error'){
                        $('.modal #pesan-error').html(`
                            <div class="alert alert-danger">
                                <strong>Error : </strong>${res.message ? res.message : ''}
                                <button class="close" data-tombol="remove-alert">&times;</button>
                            </div>
                        `);
                    }else if(res.status === 'success'){
                        $('.modal #pesan-error').html(`
                            <div class="alert alert-success">
                                <strong>Success : </strong>${res.message ? res.message : ''}
                                <button class="close" data-tombol="remove-alert">&times;</button>
                            </div>
                        `);
                        setTimeout(()=>{
                            that.props.dispatch(e=>{
                                e({type:'TOGGLE_MODAL2',value:false})
                            })
                            that.___loadData();
                        },1000)
                    }
                }
            });
        })

        //===============================================================
        
    }
    //===========================================================
    ___loadData(){
        let {loginInfo} = this.props;
        fetch(window.helmi.api+'api/v1/player/datatable',{
            headers: {
              'Content-Type':'application/x-www-form-urlencoded',
              'Authorization':'Bearer '+ loginInfo.user.api_token,
            },
            method:'GET', 
        }).then( res => res.json() )
        .then( res => { 
           if(res.status === 'success'){
                return Promise.resolve(res);
           }else if(res.status === 'error'){
               if(!res.data){
                res.data ={
                    alert:[res.message]
                }
               }
            return Promise.reject(res);
           }
            
        })
        .then( res => {
             if(res.data){
                this.setState({listData:res.data})
             }
        })
        .catch( res =>{
            let baru=[];
            if(!res.data)baru.push(res.message)
            else{
                baru = res.data
            }
            //console.log(error)
            this.setState({
                errorList:baru.alert
            })
        })
    }
    handleClickPage(e){
        e.preventDefault()
        this.props.dispatch({type:'update_page',value:'game'})
    }
    handleClickLogout(){
        this.props.dispatch({type:'RESET'})
    }
    render(){
        let dataTable
        if(this.state.listData){
            dataTable = this.state.listData.map( val =>{
                return <tr key={val.id}>
                   <td>{val.id}</td> 
                   <td>{val.player_name}</td> 
                   <td>{val.game_name}</td> 
                   <td>
                       <button data-tombol="modify-point" data-point={val.point} data-id={val.id} className="btn btn-warning">Points</button>
                       <button data-tombol="hapus" data-id={val.id} className="btn btn-default">delete</button>
                       <button data-tombol="edit" data-id={val.id} className="btn btn-info">edit</button>
                    </td> 
                </tr>
            })
        }else{
            dataTable = <tr >
                <td colSpan={4} ><h4>Tidak ada data</h4></td>
            </tr>
        }
        return(
            <div className="container body">
            <div className="main_container">
                <div className="col-md-12 " style={{width:'100%'}}>
                
                    <div className="right_col" role="main">
                    <div className="row">
                    <div className="clearfix"></div>
                        <div className="row">
                        <div className="col-md-12 col-sm-12 col-xs-12">
                            <div className="x_panel">
                            <div className="x_title">
                                <h2>Player List</h2> 
                                <div className="clearfix"></div>
                            </div>
                            <div className="x_content">
                            <div className="row">
                                <div>
                                    <button className="btn btn-danger" data-tombol="tambah">Tambah</button>
                                    &nbsp; <button onClick={this.handleClickPage.bind(this)} className="btn btn-default">Game page</button>
                                    <div className="pull-right">
                                        <button onClick={this.handleClickLogout.bind(this)} className="btn btn-danger">Logout</button>
                                    </div>
                                </div>
                                <table id="datatablez" className="table table-striped table-bordered">
                                        <thead>
                                            <tr>
                                            <th> ID Player</th>
                                            <th> Nama Player </th>
                                            <th> Nama Game yang dimainkan </th> 
                                            <th>Action</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                        {dataTable}
                                        </tbody>
                                </table>
                            </div>
                            <ModalPage />

                            </div>
                        </div>
                    </div>
                    </div>
                    </div>
                </div> 
            </div> 
            </div>
            </div> 
        );
    }
}
export default connect(store=>store)(Player)


